.description {
    display: flex;
    flex-direction: row;
    justify-content: space-around;

    h1 {
        margin-top: 30px;
        font-weight: 500;
        font-size: 36px;
        color: #FF6060;
        margin-bottom: 0;
    }

    .location {
        margin-top: 2px;
        font-weight: 500;
        font-size: 18px;
        color: #FF6060;
        margin-bottom: 20px;
    }

    .tags {
        display: flex;
        flex-direction: row;
        
        p {
            border-radius: 10px;
            background-color: #FF6060;
            color: white;
            font-weight: 500;
            font-size: 14px;
            padding: 6px 14px;
            margin-right: 10px;
            margin-bottom: 0;
        }
    }
}

@media (max-width: 820px) {
    .description {
        display: block;
        margin-left: auto;
        margin-right: auto;
        width: 80%;
    }

    .ownerWithStars {
        margin-top: 20px;
        display: flex;
        flex-direction: row-reverse;
        justify-content: space-between;
    }
}